import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SignupService } from '../service/SignupService.service';
@Component({
  selector: 'app-signup-success-component',
  templateUrl: './signup-success-component.component.html',
  styleUrls: ['./signup-success-component.component.css']
})
export class SignupSuccessComponentComponent implements OnInit {

   public user: string;

          constructor(public router: Router, private signupService: SignupService) { 
          }

          ngOnInit() {
          this.user= this.signupService.getUser()
              console.log(this.user);
           
          }

}



